/* http://meyerweb.com/eric/tools/css/reset/
   v3.0 | 20180413
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Oswald");
@import url("https://fonts.googleapis.com/css?family=Material+Icons");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box;
  transition: background .3s, color .3s, filter .3s; }

a {
  color: inherit;
  text-decoration: none; }
  a:focus {
    outline: none;
    border-color: transparent; }

h1, figure, fieldset {
  margin: 0;
  padding: 0; }

input, select, textarea, option {
  -webkit-appearance: none;
  border-radius: 0;
  border: 0;
  padding: 0; }
  input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active, option:focus, option:active {
    outline: 0; }

label {
  cursor: pointer;
  user-select: none; }

fieldset {
  border: none; }

html {
  font-size: 62.5%; }

body, html {
  width: 100%;
  height: 100%;
  line-height: 1;
  font-family: "Oswald", sans-serif; }

body {
  overflow-x: hidden;
  font-size: 1.4rem; }

button {
  cursor: pointer;
  font-family: inherit;
  color: inherit;
  appearance: none;
  border: 0;
  padding: 0;
  background: transparent;
  letter-spacing: 0.4px;
  line-height: 1; }
  button:focus, button:active {
    appearance: none;
    outline: none; }

img {
  max-width: 100%;
  vertical-align: bottom; }

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%; }

main {
  flex: 1 0 auto; }

footer {
  flex: 0 0 auto; }

.content {
  flex: 1 0 auto; }

.container {
  width: 100%;
  margin: 0 auto; }
  @media screen and (max-width: 768px) {
    .container {
      padding: 0 20px; } }
  @media screen and (min-width: 768px) {
    .container {
      max-width: 720px;
      padding: 0; } }

header {
  background: #C2EFEB;
  padding: 20px 0; }
  header .title {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  header h1 {
    font-size: 2rem;
    color: #736477; }
  header .icon .material-icons {
    color: #EEFAF9;
    font-size: 3.5rem;
    border-radius: 50%;
    padding: 5px;
    background: #736477; }

section:not(:last-child) {
  margin-bottom: 20px; }

main {
  padding: 40px 0; }

.form {
  display: flex; }

.task-form {
  justify-content: space-between; }

.field-wrap {
  display: inline-block; }

.field {
  padding: 0 5px;
  height: 35px;
  border-radius: 3px;
  border: 1px solid #E7E7E7; }

.btn {
  height: 35px;
  display: flex;
  justify-content: center;
  color: #ffffff;
  background: #736477;
  border-radius: 3px; }
  .btn:hover {
    background: #665869; }

.btn-group {
  display: flex;
  align-items: center; }

.btn-sm {
  display: flex;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-left: 10px;
  color: #ffffff; }
  .btn-sm .material-icons {
    font-size: 2rem; }

.btn-status {
  background: #EEC780; }
  .btn-status:hover {
    background: #ebbd6a; }

.btn-edit {
  background: #7BBEC3; }
  .btn-edit:hover {
    background: #69b5bb; }

.btn-delete-item {
  background: #E27069; }
  .btn-delete-item:hover {
    background: #de5c54; }

.btn-save {
  background: #5ACBA5; }
  .btn-save:hover {
    background: #47c59a; }

.btn-cancel {
  background: #7BBEC3; }
  .btn-cancel:hover {
    background: #69b5bb; }

.not-progress .btn-status {
  opacity: .5; }
  .not-progress .btn-status:hover {
    opacity: .7; }

.inprogress .btn-status {
  opacity: 1; }

.status-cntrl:checked + .name-field {
  text-decoration: line-through; }

.complete .btn-status {
  display: none; }

.btn-lg {
  padding: 0 10px;
  margin-right: 10px; }

.controls-task-main {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media screen and (max-width: 767px) {
    .controls-task-main {
      flex-direction: column;
      align-items: flex-start; }
      .controls-task-main .control-item:not(:last-child) {
        margin-bottom: 10px; } }
  .controls-task-main .btn {
    width: 35px; }

.filter-task {
  cursor: pointer;
  min-width: 215px;
  border: 1px solid #E7E7E7;
  text-transform: uppercase;
  position: relative;
  border-radius: 3px; }
  .filter-task:hover .filter-select {
    display: block; }
  .filter-task .filter-btn {
    position: relative;
    display: block; }
    .filter-task .filter-btn::after {
      content: '>';
      position: absolute;
      top: 0;
      right: 0;
      height: 15px;
      width: 35px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      transform: rotate(90deg); }
  .filter-task .filter-select {
    background: #ffffff;
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border: 1px solid #E7E7E7;
    border-top: 0;
    margin-top: 36px;
    z-index: 2; }
  .filter-task .filter-option {
    display: flex;
    align-items: center;
    padding: 5px 10px;
    height: 35px; }
    .filter-task .filter-option:hover {
      background: #E3DFE4; }

@media screen and (min-width: 767px) {
  .tasks-container {
    width: 50%; } }

.tasks-wrap {
  border: 2px solid #e7e7e7;
  padding: 10px;
  border-radius: 3px; }
  .tasks-wrap:not(:last-child) {
    margin-bottom: 10px; }

.task-normal .status-cntrl {
  width: 13px;
  height: 13px;
  border: 1px solid #E7E7E7;
  margin-right: 10px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer; }
  .task-normal .status-cntrl:checked {
    border: 1px solid #5ACBA5; }
    .task-normal .status-cntrl:checked:after {
      content: '';
      display: block;
      width: 3px;
      height: 10px;
      margin-left: 3px;
      margin-top: -2px;
      border: solid #5ACBA5;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      top: 0; }

.task-normal .name-field {
  border: 0;
  height: 35px;
  align-items: center;
  display: inline-flex;
  overflow: hidden;
  text-overflow: ellipsis; }
  @media screen and (max-width: 767px) {
    .task-normal .name-field {
      max-width: 77px; } }

footer {
  background: #736477;
  color: #ffffff;
  padding: 30px;
  font-size: 1.2rem; }
  footer .copyright {
    display: flex;
    justify-content: space-between; }
